import * as React from "react";
import { StaticImage } from "gatsby-plugin-image";
import Helmet from "react-helmet";
import Menu from "../components/Menu";
import Footer from "../components/Footer";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";

const Contacto = () => {
  return (
    <main>
      <Helmet>
        <title>Contacto</title>
        <meta
          name="keywords"
          content="vision, andina, podcast, fm, radio, frecuencias, ecuador"
        />
        <meta
          name="description"
          content="Somos una radio corporativa que se enfoca en la digitalizaci칩n medi치tica, basada en el consumo comunicacional interactivo de contenidos."
        />
      </Helmet>
      <Menu />
      <Container fluid className="background">
        <Row>
          <Col md={12} className="px-0 d-flex justify-content-center">
            <Card className="bg-dark text-white rounded-0 border-0">
              <StaticImage
                src="../images/programacion/lachismosa-programacion.jpg"
                alt="Lachismosa"
                className="main-banner w-100 d-none d-sm-block"
              />
              <StaticImage
                src="../images/programacion/lachismosa-programacion-sm.jpg"
                alt="Lachismosa"
                className="main-banner w-100 d-block d-sm-none"
              />
              <Card.ImgOverlay className="rounded-0 d-flex align-items-center p-0">
                <Container>
                  <Row>
                    <Col md={12}>
                      <div class="content-banner">
                        <h3 class="titulo-banner">Programaci칩n</h3>
                        <p class="texto-banner">
                          Transmitimos la mejor <br />
                          programaci칩n musical <br />y de entretenimiento.
                        </p>
                      </div>
                    </Col>
                  </Row>
                </Container>
              </Card.ImgOverlay>
            </Card>
          </Col>
        </Row>
      </Container>
      <Footer />
    </main>
  );
};

export default Contacto;
